import { cilPencil, cilTrash } from '@coreui/icons';
import CIcon from '@coreui/icons-react';
import { CButton, CCard, CCardBody, CTable, CTableBody, CTableDataCell, CTableHead, CTableHeaderCell, CTableRow } from '@coreui/react'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import { DataTable } from 'src/components';
import { ToastContainer, toast } from 'react-toastify'

const ProductList = () => {
  const [products, setProduct] = useState([]);
  const [updateList, setUpdateList] = useState({}) 

  useEffect(() => {
    var requestOptions = {
      method: 'POST',
      redirect: 'follow'
    };
    
    let url =  process.env.REACT_APP_API_URL+"product/list"
    fetch(url, requestOptions)
      .then(response => response.json())
      .then(result => { console.log(result.data); setProduct(result.data) })
      .catch(error => console.log('error', error));

  }, [updateList]);

  const handleDelete = (arg)=>{
    let requestOptions = {
      method: 'POST',
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        "_id": arg
      }),
    };
    const confirmWrp =  confirm("Are you sure you want to delete this?");
    if(!confirmWrp){
      return;
    }
    fetch(process.env.REACT_APP_API_URL+"brand/delete", requestOptions)
    .then(response => response.json())
    .then(result => {
      if(result.status==200){
        toast.success(result.message);
        setUpdateList({"_id": arg});
      }else{
        toast.error(result.message);
      }
    })
    .catch(error => console.log('error', error)); 
  }

  return (
    <>
      <div className='d-flex justify-content-between  align-items-center  mb-4'>
        <h1 className='headingType1'>Product Listing</h1>
        <Link to={'/products/add'} className='btn btn-primary'>Add</Link>
      </div>

      <CCard>
        <CCardBody>
          <CTable>
            <CTableHead>
              <CTableRow>                
                <CTableHeaderCell width={60}>
                  Image
                </CTableHeaderCell>
                <CTableHeaderCell  width={100}>
                  Product Id
                </CTableHeaderCell>
                <CTableHeaderCell width={160}>
                  Product Name
                </CTableHeaderCell>
                <CTableHeaderCell width={120}>
                  Sku
                </CTableHeaderCell>
                <CTableHeaderCell >
                 Short Description
                </CTableHeaderCell>
                <CTableHeaderCell width={150}>
                 Status
                </CTableHeaderCell>
                <CTableHeaderCell  width={100}>
                 Action
                </CTableHeaderCell>
              </CTableRow>
            </CTableHead>
            <CTableBody>
              {products.map((i, key)=>(
                <CTableRow key={key}>                  
                  <CTableDataCell>
                    <img src={process.env.REACT_APP_BASE_URL+i.photo.image} width={100} style={{display:'block'}}/>                   
                  </CTableDataCell>
                  <CTableDataCell>
                    {i.product_id}
                  </CTableDataCell>
                  <CTableDataCell>
                    {i.product_name}               
                  </CTableDataCell>
                  <CTableDataCell>
                    {i.sku}               
                  </CTableDataCell>
                  <CTableDataCell>
                    {i.short_description}               
                  </CTableDataCell>
                  <CTableDataCell>                    
                    {i.status==0?<CButton color='warning'>Inactive</CButton>:<CButton color='success'>Active</CButton>}               
                  </CTableDataCell>
                  <CTableDataCell> 
                    <ul  className='actionBtnS'>
                      <li>                        
                        <CIcon onClick={()=>handleDelete(i._id)} icon={cilTrash} customClassName="nav-icon" />
                      </li>
                      <li>
                        <Link to={'/products/'+i._id}>
                          <CIcon icon={cilPencil} customClassName="nav-icon" />
                        </Link>
                      </li>
                    </ul> 
                  </CTableDataCell>
                </CTableRow>
              ))}
            </CTableBody>
          </CTable>



        </CCardBody>
      </CCard>
    </>
  )
}

export default ProductList
